@font-face {
  font-family: "PT Sans";
  font-display: swap;
  font-weight: 400;
  font-style: normal;
  src: url(./fonts/PTSans-Regular.woff2) format("woff2");
}
@font-face {
  font-family: "PT Sans";
  font-display: swap;
  font-weight: 700;
  font-style: normal;
  src: url(./fonts/PTSans-Bold.woff2) format("woff2");
}
.page {
  font-family: "PT Sans", sans-serif;
  color: #000;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
  min-inline-size: 1920px;
  margin: 0 auto;
  padding: 0 312px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-block: 40px;
}

.header__nav-list {
  display: flex;
  gap: 30px;
  list-style: none;
}

.header__link {
  text-decoration: none;
  color: #000;
}

.header__button-search {
  max-inline-size: 25px;
  max-block-size: 25px;
  border: 1px solid black;
  background-color: transparent;
  padding: 0;
  margin: 0;
}

.custom-select {
  position: relative;
}

.header__select {
  display: flex;
  align-items: center;
  position: relative;
}

.custom-select::after {
  content: "";
  position: absolute;
  display: block;
  inset: 5px 28px;
  width: 10px;
  height: 10px;
  background-color: white;
}

.custom-select::after {
  content: "";
  position: absolute;
  display: block;
  inset: 5px 28px;
  width: 10px;
  height: 10px;
  background-image: url(./images/arrow.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  pointer-events: none;
}

.header__search-form {
  display: flex;
  align-items: center;
  gap: 20px;
}

.header__input-search {
  min-block-size: 25px;
}

.header__logo {
  display: block;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li {
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  inline-size: 1px;
  block-size: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  white-space: nowrap;
}